/** @jsx jsx */
import { jsx } from '@emotion/core';
import Page from 'src/layout/Page';
import { Container, Row, Col } from 'react-bootstrap';
import { useBlogFeeds } from 'src/hooks';
import BlogFeedCard from 'components/BlogFeedCard';
import Loading from 'components/Loading';

const Blogs = () => {
  const { feeds, isFetched } = useBlogFeeds();

  return (
    <Page pageName="Blogs" description="루비콘 멤버들의 개인 블로그에 업데이트 되는 포스팅들">
      <Container>
        {isFetched ? null : <Loading text="루비콘 멤버들의 블로그 포스팅을 불러오는 중 입니다..." />}
        <Row>
          {feeds.map((feed, index) => {
            return (
              <Col key={index} xs={12} sm={6} md={4} lg={3}>
                <BlogFeedCard
                  title={feed.title}
                  link={feed.link}
                  authorProfile={feed.author.profileImg}
                  authorName={feed.author.name}
                  content={feed.contentSnippet}
                  pubDate={feed.pubDate}
                />
              </Col>
            );
          })}
        </Row>
      </Container>
    </Page>
  );
};

export default Blogs;
