/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { useMemo } from 'react';
import formatDistanceToNow from 'date-fns/formatDistanceToNow';
import koLocale from 'date-fns/locale/ko';
import Card from 'src/components/Card';
import AnimatedListItem from 'src/components/Animated/AnimatedListItem';

interface Props {
  link: string;
  title: string;
  authorProfile: string;
  authorName: string;
  content: string;
  pubDate: string;
}
const BlogFeedCard = ({ link, title, authorProfile, authorName, content, pubDate }: Props) => {
  const ellipsedContent = useMemo(() => {
    return `${content.substring(0, 100)}...`;
  }, [content]);
  const publishedDate = useMemo(() => {
    return formatDistanceToNow(new Date(pubDate), {
      addSuffix: true,
      locale: koLocale,
    });
  }, [pubDate]);

  return (
    <AnimatedListItem>
      <a href={link} target="_blank" rel="noopener noreferrer">
        <Card>
          <Card.Body>
            <Card.Title>{title}</Card.Title>
            <Card.Subtitle className="text-muted">
              <img src={authorProfile} alt={authorName} />
              {authorName}
            </Card.Subtitle>
            <Card.Text>{ellipsedContent}</Card.Text>
            <p
              css={css`
                text-align: right;
                font-size: 0.6rem;
                margin: 0;
              `}
            >
              {publishedDate}
            </p>
          </Card.Body>
        </Card>
      </a>
    </AnimatedListItem>
  );
};

export default BlogFeedCard;
